/**
 * https://github.com/hinok/tasty-burger
 */
.burger-icon {
  display: inline-block;
  font-size: 0;
  cursor: pointer;
  vertical-align: middle;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.burger-icon, .burger-icon::after, .burger-icon::before {
  box-sizing: border-box;
}

.burger-icon span {
  position: relative;
}

.burger-icon span, .burger-icon span::before, .burger-icon span::after {
  display: block;
  width: 100%;
  transition: all 0.3s;
  background: #fff;
  backface-visibility: hidden;
}

.burger-icon span::before, .burger-icon span::after {
  position: absolute;
  content: '';
}

.burger-icon--x span {
  background: transparent;
}

.burger-icon--sm {
  width: 14.4px;
  height: 12px;
}

.burger-icon--sm span {
  margin: 5px 0 0;
}

.burger-icon--sm span, .burger-icon--sm span::before, .burger-icon--sm span::after {
  height: 2px;
}

.burger-icon--sm span::before {
  top: -5px;
}

.burger-icon--sm span::after {
  top: 5px;
}

.burger-icon--sm.burger-icon--x span::before {
  transform: translate(0, 5px) rotate(-45deg);
}

.burger-icon--sm.burger-icon--x span::after {
  transform: translate(0, -5px) rotate(45deg);
}

.burger-icon--md {
  width: 21.6px;
  height: 18px;
}

.burger-icon--md span {
  margin: 8px 0 0;
}

.burger-icon--md span, .burger-icon--md span::before, .burger-icon--md span::after {
  height: 2px;
}

.burger-icon--md span::before {
  top: -8px;
}

.burger-icon--md span::after {
  top: 8px;
}

.burger-icon--md.burger-icon--x span::before {
  transform: translate(0, 8px) rotate(-45deg);
}

.burger-icon--md.burger-icon--x span::after {
  transform: translate(0, -8px) rotate(45deg);
}

.burger-icon--lg {
  width: 52.8px;
  height: 44px;
}

.burger-icon--lg span {
  margin: 21px 0 0;
}

.burger-icon--lg span, .burger-icon--lg span::before, .burger-icon--lg span::after {
  height: 2px;
}

.burger-icon--lg span::before {
  top: -21px;
}

.burger-icon--lg span::after {
  top: 21px;
}

.burger-icon--lg.burger-icon--x span::before {
  transform: translate(0, 21px) rotate(-45deg);
}

.burger-icon--lg.burger-icon--x span::after {
  transform: translate(0, -21px) rotate(45deg);
}

/**
 * Primary Button
 * Usage :
    <a class="btn" href="#">
        <span>Lorem ipsum</span>
        <svg class="arrow-next-icon" viewBox="0 0 1 1"><use xlink:href="{{ 'assets/public/images/icons.svg#arrow-next'|theme }}" /></use></svg>
    </a>
*
 */
.btn {
  position: relative;
  display: inline-block;
  outline: solid 1px white;
  outline-offset: -2px;
  padding: 8px 82px 8px 34px;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 20px;
  font-size: 2rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  transition: color .3s;
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

.btn:before {
  content: '';
  position: absolute;
  top: 0;
  left: -4px;
  width: 150%;
  height: 100%;
  background: white;
  z-index: -1;
  transform: rotate3d(0, 0, 1, -45deg) translate3d(0, -3em, 0);
  transform-origin: 0% 100%;
  transition: transform 0.3s, opacity 0.3s, background-color 0.3s;
}

.btn svg[class$="-icon"] {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  width: 30px;
  transition: fill 0.3s, transform .3s;
}

.btn:hover {
  color: #182f2e;
}

.btn:hover:before {
  opacity: 1;
  background-color: white;
  transform: rotate3d(0, 0, 1, 0deg);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

.btn:hover svg[class$="-icon"] {
  fill: #182f2e;
  transform: translate(8px, -50%);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

/**
 * @section Grid
 * Structure and layout
 */
/**
 * Base grid styles: single column
 */
.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1136px;
  width: 88%;
}

.row {
  margin-left: -2vw;
  margin-right: -2vw;
}

.grid-fourth, .grid-third, .grid-half, .grid-two-thirds, .grid-three-fourths, .grid-full, .grid-dynamic {
  float: left;
  padding-left: 2vw;
  padding-right: 2vw;
  width: 100%;
}

/**
 * Reverses order of grid for content choreography
 */
.grid-flip {
  float: right;
}

/**
 * Add columns to grid on bigger screens
 */
@media (min-width: 20em) {
  .row-start-xsmall .grid-fourth {
    width: 25%;
  }
  .row-start-xsmall .grid-third {
    width: 33.3333333333%;
  }
  .row-start-xsmall .grid-half {
    width: 50%;
  }
  .row-start-xsmall .grid-two-thirds {
    width: 66.6666666667%;
  }
  .row-start-xsmall .grid-three-fourths {
    width: 75%;
  }
  .row-start-xsmall .grid-full {
    width: 100%;
  }
}

@media (min-width: 30em) {
  .row-start-small .grid-fourth {
    width: 25%;
  }
  .row-start-small .grid-third {
    width: 33.3333333333%;
  }
  .row-start-small .grid-half {
    width: 50%;
  }
  .row-start-small .grid-two-thirds {
    width: 66.6666666667%;
  }
  .row-start-small .grid-three-fourths {
    width: 75%;
  }
  .row-start-small .grid-full {
    width: 100%;
  }
}

@media (min-width: 40em) {
  .grid-fourth {
    width: 25%;
  }
  .grid-third {
    width: 33.3333333333%;
  }
  .grid-half {
    width: 50%;
  }
  .grid-two-thirds {
    width: 66.6666666667%;
  }
  .grid-three-fourths {
    width: 75%;
  }
  .grid-full {
    width: 100%;
  }
  .offset-fourth {
    margin-left: 25%;
  }
  .offset-third {
    margin-left: 33.3333333333%;
  }
  .offset-half {
    margin-left: 50%;
  }
  .offset-two-thirds {
    margin-left: 66.6666666667%;
  }
  .offset-three-fourths {
    margin-left: 75%;
  }
  .offset-full {
    margin-left: 100%;
  }
}

/**
 * Dynamic grid
 */
@media (min-width: 20em) {
  .grid-dynamic {
    width: 50%;
  }
}

@media (min-width: 30em) {
  .grid-dynamic {
    width: 33.3333333333%;
  }
}

@media (min-width: 40em) {
  .grid-dynamic {
    width: 25%;
  }
}

